import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { LevelService } from 'src/app/service/level.service';

@Component({
  selector: 'app-level-view',
  templateUrl: './level-view.component.html'
})
export class LevelViewComponent implements OnInit {

  viewId: any;
  objectArray: any;
  popup: Window;

  constructor(
    private activatedRoute: ActivatedRoute,
    private levelservice: LevelService
  ) {

    if (this.activatedRoute.snapshot.paramMap.get('id')) {
      this.viewId = this.activatedRoute.snapshot.paramMap.get('id');
    }
  }
  ngOnInit(): void {
    this.levelservice.getLevelById(this.viewId).subscribe((response) => {
      if (response.success) {
        this.objectArray = response.data;
      }
    });
  }

  VoucherSourcetoPrint() {
    return (
      `
            <html>
            <head>
              <script>
                function step1() {
                  setTimeout('step2()', 10);
                }
                function step2() {
                  window.print(); window.close()
                }
              </script>
            </head>

            <body onload='step1()'>
              <h3>Level Name:- ${this.objectArray?.level_name}</h3>
              <h3>Level Code:- ${this.objectArray?.level_code}</h3>
              <img src='${this.objectArray?.level_bar_code_image}' />
            </body>
            </html>
      `
    );
  }
  VoucherPrint() {
    // let pwa = window.open();
    // pwa.document.open();
    // pwa.document.write(this.VoucherSourcetoPrint());
    // pwa.document.close();
    window.print();
  }


}

