import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { Routes, RouterModule } from '@angular/router';
import { SearchModule } from 'src/app/common-module/search/search.module';
import { BtnAddModule } from 'src/app/common-module/btn-add/btn-add.module';
import { TableListModule } from 'src/app/common-module/table-list/table-list.module';

import { NgSelectModule } from '@ng-select/ng-select';
import { SharedModule } from 'src/app/shared/shared.module';
import { FormValidationModule } from 'src/app/shared/form-validation/form-validation.module';
import { ReactiveFormsModule, FormsModule } from '@angular/forms';
import { PaginationModule } from 'src/app/common-module/pagination/pagination.module';
import { LevelListComponent } from './level-list/level-list.component';
import { AddLevelComponent } from './add-level/add-level.component';
import { LevelService } from 'src/app/service/level.service';
import { LevelViewComponent } from './level-view/level-view.component';

const routes: Routes = [
  {
    path: "",
    component: LevelListComponent,
    data: { title: 'level_list' }
  },
  {
    path: "add",
    component: AddLevelComponent,
    data: { title: 'add_level' }
  },
  {
    path: 'edit/:id',
    component: AddLevelComponent,
    data: { title: 'edit_level' }
  },
  {
    path: 'view/:id',
    component: LevelViewComponent,
    data: { title: 'view_level' }
  },


]

@NgModule({
  declarations: [LevelListComponent, AddLevelComponent, LevelViewComponent],
  imports: [
    CommonModule,
    RouterModule.forChild(routes),
    TableListModule,
    SearchModule,
    BtnAddModule,
    FormsModule,
    ReactiveFormsModule,
    FormValidationModule,
    SharedModule,
    NgSelectModule,
    PaginationModule
  ], providers: [LevelService]
})
export class LevelModule { }
